import * as React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';

const CardsIndex = ({ data, location }) => {
    const siteTitle = data.site.siteMetadata?.title || `Title`
    const posts = data.allMarkdownRemark.nodes

    const centered = {
        margin: '0 auto'
    }
    // --------------------------------------------------------------------
    return (
        <Layout location={location} title={siteTitle}>
            <div style={centered}>
                <ImageList sx={{ width: 600, height: 700 }} cols={3} rowHeight={300}>
                    {posts.map(post => {
                        console.log(post)
                        const src_url = `/cards/${post.frontmatter.id}.png`;
                        const jump_url = `/${post.frontmatter.id}`;
                        return (
                            <a href={jump_url}>
                                <ImageListItem key={post.frontmatter.id}>
                                    <img
                                        src={src_url}
                                        alt={post.frontmatter.name}
                                        loading="lazy"
                                    ></img>
                                    <ImageListItemBar
                                        title={post.frontmatter.name}
                                        subtitle={post.frontmatter.id}
                                        position="below"
                                    />
                                </ImageListItem>
                            </a>
                        )
                    })}
                </ImageList>
            </div>
            {/* type
          package-num
          name
          card-type
          keyword
          attribute
          race
          lx
          jq
          ack
          effect
          id */}
        </Layout>
    )
}

export default CardsIndex




/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
        filter: { frontmatter: { type: { eq: "card" } } }
        sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          id
          name
        }
      }
    }
  }
`
